.company-facts {
    @import "variables";

    background: transparent url('../../../images/facts-bg.png') center repeat-x;
    padding: $facts-padding;
    margin-bottom: $facts-margin-bottom;

    .facts-list {
        padding: 0;
        font-size: 0;
        line-height: 0;
        list-style: none;
        text-align: center;
        text-justify: newspaper;
        -ms-text-align-last: center;
        text-align-last: center;
    }

    .fact {
        text-align: center;
        -ms-text-align-last: center;
        text-align-last: center;
        font-size: $facts-list-item-font-size;
        line-height: $facts-list-item-line-height;
        display: inline-block;
        width: (100% / $facts-list-item-count - 1%);
    }

    .image {
        display: inline-block;
        max-width: $facts-image-max-width;
        vertical-align: middle;
        height: 155px;

        img {
            display: inline-block;
            width: 100%;
            max-height: 100%;
        }
    }

    .name {
        font-family: $font-family-default;
        line-height: 1.2;
        height: 1.2 * 2em;
        font-size: 18px;
        font-weight: 700;
    }

    .value {
        font-family: $font-family-marked;
        font-size: $facts-list-item-value-font-size;
        color: $facts-list-item-value-font-color;
        line-height: 1;
        margin: 0;
    }

    .object {
        font-family: $font-family-default;
        font-size: 18px;
        font-weight: 700;
    }
}



