.company-prices {
    @import "variables";

    background: url('../../../images/price-bg.jpg') center repeat-x;
    padding: $prices-padding;
    margin-bottom: $prices-margin-bottom;

    .prices-list {
        counter-reset: prices;
        display: block;
        list-style: none;
        border: $prices-list-border-width solid $prices-list-border-color;
        border-radius: $prices-list-border-radius;
        padding: $prices-list-padding;
        margin: 20px 0 $prices-list-margin-bottom;
        position: relative;

        &:before {
            content: '';
            position: absolute;
            display: block;
            width: $prices-list-icon-size;
            height: $prices-list-icon-size;
            top: 50%;
            margin-top: -($prices-list-icon-size / 2);
            left: ($prices-list-left-padding - $prices-list-icon-size) / 2;
            background: transparent url('../../../images/icons/conversation.png') center no-repeat;
        }
    }

    .pointer {
        display: none;
        background: $prices-list-pointer-color;
        width: 157px;
        height: 1px;
        position: relative;

        &:after {
            content: '>';
            display: block;
            position: absolute;
        }
    }

    .price {
        margin-bottom: $prices-list-item-line-space;
        position: relative;

        &:before {
            counter-increment: prices;
            content: counter(prices);
            width: $prices-list-item-counter-size;
            height: $prices-list-item-counter-size;
            margin-right: $prices-list-item-counter-margin-right;
            text-align: center;
            font-family: $font-family-marked;
            line-height: $prices-list-item-counter-size;
            display: inline-block;
            background: $prices-list-item-counter-bg-color;
            color: $prices-list-item-counter-font-color;
        }

        &:nth-child(2n) {
            background: rgba($main-color, 0.2);
        }

        .service-name {
            font-weight: 700;
        }
    }

    .value {
        @include vcenter();
        right: 0;

        .amount {
            font-family: $font-family-marked;
            font-size: $prices-list-item-value-amount;
        }
    }

    .action-button {
        width: $prices-list-action-button-size;
        margin: 0 auto;
    }
}



