.company-advantages {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        .image {
            margin-left: 22px;
            margin-right: 0;
            width: 400px;
        }

        .advantages-list {
            margin-left: 460px;
        }
    }

    @media #{$adaptive-breakpoint-m} {

        .image {
            display: none;
        }

        .advantages-list {
            margin-left: 0;
        }

        .description {
            padding-left: 47px;
            margin: 40px 0 10px;
            border-radius: 5px;

            &:before,
            &:after {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-s} {
        margin-bottom: 30px;

        .image {
            display: none;
        }

        .advantages-list {
            margin-left: 0;
        }

        .description {
            padding-left: 47px;
            margin: 40px 0 10px;
            border-radius: 5px;

            &:before,
            &:after {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-xs} {
        margin-bottom: 30px;

        .image {
            display: none;
        }

        .advantages-list {
            margin-left: 0;
            padding-left: 10px;
        }

        .description {
            padding: 13px;
            margin: 40px 10px 10px;
            border-radius: 5px;

            &:before,
            &:after,
            .text:before,
            .text:after {
                display: none;
            }

            .text {
                padding: 15px;
            }
        }

    }
}



