.main-promo {
    @import "variables";

    padding-top: $promo-padding-top;
    height: $promo-height;
    margin: $promo-margin;
    box-sizing: border-box;

    .content {
        padding-right: $promo-padding-right;
        box-sizing: border-box;

        & > * {
            position: relative;
            z-index: 1;
        }
    }

    .line {
        display: block;
        margin: $promo-line-margin;
        width: $promo-line-width;
        height: $promo-line-height;
        background: $promo-line-color;
        overflow: hidden;
        text-indent: -9999px;
    }

    .image {
        display: block;
        position: absolute;
        top: $promo-image-position-top;
        right: 0;
        width: $promo-image-width;
        height: $promo-image-height;
        z-index: 0;
    }

    .description {
        font-family: $font-family-default;
        font-size: $promo-description-font-size;
        text-align: center;
    }

    .action-button {
        width: $promo-button-width;
        margin: $promo-button-margin;
    }

    @each $source in $sources {

        &.#{$source} {
            background: url('../../../images/multiland/promo/#{$source}/bg.png') center repeat-x;
            background-size: cover;

            .image {
                background: url('../../../images/multiland/promo/#{$source}/image.png') center no-repeat;
            }
        }

    }

    &.printers {

        .content {
            padding-right: 550px;
        }

        .headline {
            font-size: 50px;

            small {
                margin-top: 0.5em;
                display: block;
                font-size: 35px;
                line-height: 1;
            }

            .side {
                font-size: 38px;
            }
        }

        .image {
            width: 651px;
            height: 487px;
            right: -100px;
        }

    }

    &.viruses,
    &.routes,
    &.computers {

        .headline {

            small {
                margin-top: 0.5em;
                display: block;
                font-size: 27px;
                line-height: 1.1;
            }

            .side {
                font-size: 38px;
            }
        }

        .image {
            right: -200px;
        }

    }

}



