.company-contacts {
    @import "variables";

    border-top: $contacts-separator-width solid $contacts-separator-color;
    padding-top: $contacts-padding-top;
    margin-bottom: $contacts-margin-bottom;

    &:after {
        content: '.';
        display: block;
        clear: both;
        width: 100%;
        font-size: 0;
        line-height: 0;
    }

    .headline {
        margin-bottom: $contacts-headline-space-below;
    }

    .contacts-description-wrapper {
        float: left;
        width: calc(100% - #{$contacts-address-map-width} - 20px);
    }

    .address {
        float: right;
        width: $contacts-address-map-width;
        text-align: center;
        min-height: $contacts-address-map-height;
    }

    .contacts-list {
        font-family: $font-family-marked;
        text-align: left;
        margin: 0;
        padding: 0;
        font-size: $contacts-list-font-size;
        list-style: none;
    }

    .contact {
        margin-bottom: $contacts-list-item-space-below;
        display: inline-block;

        .title {
            font-family: $font-family-default;
        }

        a {
            white-space: nowrap;
        }
    }

    .image {
        text-align: center;
    }

    .description {
        font-family: $font-family-default;
    }

    .legal-info {
        font-family: $font-family-marked;
    }
}



