.company-warranties {
    @import "variables";

    margin-bottom: $warranties-margin-bottom;
    background: $warranties-bg;
    padding: $warranties-padding;

    .headline {
        line-height: $warranties-headline-line-height;
    }

    .warranties-list {
        margin-top: $warranties-list-margin-top;
        padding: 0;
        list-style: none;
        font-size: 0;
        line-height: 0;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: $warranties-list-bg-image-width;
            height: $warranties-list-bg-image-height;
            margin-left: -($warranties-list-bg-image-width / 2);
            margin-top: -($warranties-list-bg-image-height / 2);
        }
    }

    .warranty {
        position: relative;
        display: inline-block;
        width: calc(50% - #{$warranties-list-item-margin});
        box-sizing: border-box;
        padding: $warranties-list-item-padding $warranties-list-item-padding-additional $warranties-list-item-padding $warranties-list-item-padding;
        vertical-align: middle;
        margin-right: $warranties-list-item-margin;
        border: $warranties-list-item-border-width solid $main-color;
        margin-bottom: $warranties-list-item-margin;

        .image {
            display: inline-block;
            width: $warranties-list-item-image-width;
            margin-right: $warranties-list-item-image-margin;
            vertical-align: middle;
        }

        .name {
            font-size: $warranties-list-item-name-font-size;
            font-weight: 700;
            line-height: 1.2;
            width: calc(100% - #{$warranties-list-item-image-width} - #{$warranties-list-item-image-margin});
            display: inline-block;
            vertical-align: middle;
        }

        &:nth-child(2n) {
            margin-right: 0;
            margin-left: $warranties-list-item-margin;
            padding: $warranties-list-item-padding $warranties-list-item-padding $warranties-list-item-padding $warranties-list-item-padding-additional;

            .image {
                margin-right: 0;
                margin-left: $warranties-list-item-image-margin;
            }
        }

    }

    @each $source in $sources {

        &.#{$source} {

            .warranties-list:after {
                background: transparent url('../../../images/multiland/warranties/#{$source}.png') center no-repeat;
            }
        }

    }
}



