.headline {
    @import "variables";

    @media #{$adaptive-breakpoint-m} {
        font-size: 60px;

        .side {
            font-size: 40px;
        }
    }

    @media #{$adaptive-breakpoint-s} {
        font-size: 54px;

        .side {
            font-size: 33px;
        }
    }

    @media #{$adaptive-breakpoint-xs} {
        font-size: 45px;

        .side {
            font-size: 26px;
        }
    }

    @media (max-width: 400px) {
        font-size: 30px;

        .side {
            font-size: 18px;
        }
    }
}



