.main-footer {
    @import "variables";
    @import "../logo/variables";
    @import "../contacts/variables";

    @media #{$adaptive-breakpoint-l} {
        .footer-menu {
            clear: none;
        }
    }
    @media #{$adaptive-breakpoint-m} {
        height: auto;

        .logo,
        .footer-menu {
            line-height: $footer-height-m;
        }

        .small-contacts {
            top: 25%;
        }

        .footer-menu {
            margin: 0;
        }

    }

    @media #{$adaptive-breakpoint-s} {
        height: auto;

        .logo,
        .footer-menu {
            line-height: $footer-height-m;
        }

        .small-contacts {
            top: 25%;
        }

        .footer-menu {
            margin: 0;
        }

    }

    @media #{$adaptive-breakpoint-xs} {
        height: auto;

        .content {
            padding: 0 20px;
            position: relative;
        }

        .logo,
        .footer-menu {
            line-height: $footer-height-m;
        }

        .small-contacts {
            top: 25%;
            right: 20px;
        }

        .footer-menu {
            margin: 0;
        }

        .menu-item {
            display: block;
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;
            line-height: 2;
        }

    }

    @media (max-width: ($logo-width + $small-contacts-width + 70px)) {

        .logo,
        .small-contacts {
            position: static;
            padding-left: 0;
            float: none;
            margin: 0 auto;
        }

        .logo {
            margin: 20px auto;
        }

        .footer-menu {
            padding-bottom: 20px;
        }

    }
}



