.work-stages {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        .stage:after {
            right: -70px;
        }

    }

    @media #{$adaptive-breakpoint-m} {

        .stages-list {
            margin-bottom: 0;
            justify-content: space-evenly;
        }

        .stage {
            flex-basis: 27%;
            margin-bottom: 40px;

            &:after {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-s} {

        .stages-list {
            margin-bottom: 0;
            justify-content: space-evenly;
        }

        .stage {
            width: 40%;
            flex-basis: 40%;
            margin-bottom: 40px;

            &:after {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-xs} {

        .stages-list {
            margin-bottom: 0;
            justify-content: space-evenly;
        }

        .stage {
            width: 80%;
            flex-basis: 80%;
            margin-bottom: 40px;

            &:after {
                display: none;
            }
        }

    }
}



