.main-footer {
    @import "variables";
    @import "../logo/variables";
    @import "../contacts/variables";

    background: $footer-bg-color;
    color: $footer-font-color;
    height: $footer-height;

    .content {
        overflow: hidden;
    }

    .logo {
        position: relative;
        top: 20px;

        line-height: 30px;
        float: left;
        width: 120px;

        img {
            width: 100%;
        }
    }

    .footer-menu {
        margin-left: $logo-width;
        margin-right: $small-contacts-width;
        font-size: $footer-font-size;
        line-height: $footer-height;

        a {
            color: $footer-font-color;
        }

    }

    .small-contacts {
        font-size: $footer-font-size;
        @include vcenter();
        right: 0;
    }
}



