.site-menu {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {
        clear: both;

        ul {
            margin: $menu-margin-l;
        }

    }

    @media #{$adaptive-breakpoint-m} {
        clear: both;

        ul {
            margin: $menu-margin-m;
        }
    }

    @media #{$adaptive-breakpoint-s} {
        clear: both;

        ul {
            margin: 0;
        }
    }

    @media #{$adaptive-breakpoint-xs} {
        clear: both;

        ul {
            margin: 0;
        }
    }

}



