.company-contacts {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        .address {
            width: 530px;
        }

        .contacts-description-wrapper {
            width: calc(100% - 550px);
        }

    }

    @media #{$adaptive-breakpoint-m} {

        .address {
            width: 100%;
            float: none;
        }

        .contacts-description-wrapper {
            float: none;
            width: 100%;
            font-size: 0;
            line-height: 0;
        }

        .contacts-list {
            width: 70%;
            display: inline-block;
            font-size: $contacts-list-font-size;
            line-height: 1.2;
        }

        .image {
            width: 30%;
            display: inline-block;
            vertical-align: top;
            margin-top: 20px;
        }

        .description,
        .legal-info {
            font-size: 16px;
            line-height: 1.2;
        }

    }

    @media #{$adaptive-breakpoint-s} {

        .address {
            width: 100%;
            float: none;
        }

        .contacts-description-wrapper {
            float: none;
            width: 100%;
        }

        .image {
            display: none;
        }

    }

    @media #{$adaptive-breakpoint-xs} {

        .content {
            padding: 20px;
        }

        .address {
            width: 100%;
            float: none;
        }

        .contacts-description-wrapper {
            float: none;
            width: 100%;
        }

        .contacts-list {
            font-size: 18px;
        }

        .image {
            display: none;
        }

    }
}



