.action-button {
    @import "variables";

    display: block;
    background-color: $accent-color;
    width: 100%;
    height: $action-button-height;
    line-height: $action-button-height;
    border: 0;
    outline: 0;
    color: $action-button-color;
    @include default-box-shadow();
    transition: box-shadow $action-button-hover-duration;

    &:hover {
        cursor: pointer;
        @include default-box-shadow($action-button-hover-shadow-color);
    }
}



