.main-promo {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        @each $source in $sources {

            &.#{$source} {

                .content {
                    padding-right: $promo-padding-right-m;
                }

            }
        }

        .image {
            right: $promo-image-position-right-m;
        }

        &.printers .image {
            right: -360px;
        }

        &.viruses .image {
            right: -370px;
        }

    }

    @media #{$adaptive-breakpoint-m} {
        height: auto;
        max-height: none;
        background-size: cover;
        padding-bottom: 55px;
        margin-bottom: 50px;

        @each $source in $sources {

            &.#{$source} {

                .content {
                    padding-right: 0;
                }

            }
        }

        .image {
            display: none;
        }

    }

    @media #{$adaptive-breakpoint-s} {
        height: auto;
        max-height: none;
        background-size: cover;
        padding-bottom: 55px;
        margin-bottom: 50px;

        @each $source in $sources {

            &.#{$source} {

                .content {
                    padding-right: 0;
                }

            }
        }

        .image {
            display: none;
        }

    }
    @media #{$adaptive-breakpoint-xs} {
        height: auto;
        max-height: none;
        background-size: cover;
        padding: 20px 10px;
        margin-bottom: 30px;

        @each $source in $sources {

            &.#{$source} {

                .content {
                    padding-right: 0;
                }

            }
        }

        .image {
            display: none;
        }

    }

}



