.main-header {
    @import "variables";
    @import "../logo/variables";
    @import "../contacts/variables";

    @media #{$adaptive-breakpoint-l} {
        height: auto;

        .main-menu {
            margin: 0;
            line-height: 50px;
        }

        .logo {
            width: $logo-width-m;

            top: 20px;

            img {
                width: 100%;
            }
        }
    }

    @media #{$adaptive-breakpoint-m} {
        height: auto;

        .main-menu {
            margin: 0;
            line-height: 50px;
        }

        .logo {
            width: $logo-width-m;
        }

    }

    @media #{$adaptive-breakpoint-s} {
        height: auto;

        .main-menu {
            margin: 0;
            line-height: 50px;
        }

        .small-contacts {
            margin-right: 20px;
        }

    }

    @media #{$adaptive-breakpoint-xs} {
        height: auto;

        .logo {
            padding-left: 20px;
        }

        .main-menu {
            margin: 0;
        }

        .site-menu {
            margin-top: 1em;
            padding-bottom: 1em;
        }

        .menu-item {
            display: block;
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;
            line-height: 2;
        }

        .small-contacts {
            margin-right: 20px;
        }

    }

    @media (max-width: ($logo-width + $small-contacts-width + 70px)) {

        .logo,
        .small-contacts {
            padding-left: 0;
            float: none;
            margin: 0 auto;
        }

        .logo {
            line-height: 30px;
        }

    }

}



