.client-reviews {
    @import "variables";

    margin-bottom: $reviews-margin-bottom;

    .reviews-list {
        margin-top: $reviews-list-margin-top;
        padding: 0;
        font-size: 0;
        line-height: 0;
        list-style: none;
        text-align: center;
        text-justify: newspaper;
        -ms-text-align-last: center;
        text-align-last: center;
    }

    .review {
        display: inline-block;
        vertical-align: top;
        width: $reviews-list-item-width;
        font-size: $reviews-list-item-font-size;
        line-height: $reviews-list-item-line-height;
        text-align: center;
        -ms-text-align-last: center;
        text-align-last: center;
        padding: $reviews-list-item-padding;
        box-sizing: border-box;

        .headline {
            font-size: $reviews-list-item-headline-font-size;
        }
    }

    .image {
        display: block;
        width: $reviews-list-item-image-size;
        max-width: 100%;
        box-sizing: border-box;
        margin: $reviews-list-item-image-margin;
        border-radius: $reviews-list-item-image-border-radius;
        padding: $reviews-list-item-image-padding;
        border: $reviews-list-item-image-border-width solid $reviews-list-item-image-border-color;
    }

    .problem {
        font-family: $font-family-default;
        display: inline-block;
        padding: $reviews-list-item-problem-padding;
        border: $reviews-list-item-problem-border-width solid $accent-color;
        border-radius: $reviews-list-item-problem-border-radius;
    }

    .comment {
        font-family: $font-family-default;
    }

    .action-button {
        width: $reviews-action-button-width;
        margin: $reviews-action-button-margin;
    }
}



