@font-face {
    font-family: 'HelveticaNeueCyr';
    src: local('HelveticaNeueCyr-LightItalic'),
        url('helvetica-neue-cyr-lightItalic.woff2') format('woff2'),
        url('helvetica-neue-cyr-lightItalic.woff') format('woff'),
        url('helvetica-neue-cyr-lightItalic.ttf') format('truetype');
    font-weight: 300;
    font-style: italic;
}

@font-face {
    font-family: 'HelveticaNeueCyr';
    src: local('HelveticaNeueCyr-Light'),
        url('helvetica-neue-cyr-light.woff2') format('woff2'),
        url('helvetica-neue-cyr-light.woff') format('woff'),
        url('helvetica-neue-cyr-light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
}



