.company-advantages {
    @import "variables";

    margin-bottom: $advantages-margin-bottom;

    .image {
        float: left;
        position: relative;
        margin-left: $advantages-image-inner-rectangle-shift + $advantages-image-margin-left;
        margin-right: $advantages-image-margin-right;
        margin-bottom: $advantages-image-margin-bottom;
        width: $advantages-image-size;
        height: $advantages-image-size;

        &:before {
            content: '';
            position: absolute;
            width: $advantages-image-inner-rectangle-size - 2 * $advantages-image-inner-rectangle-border-width;
            height: $advantages-image-inner-rectangle-size - 2 * $advantages-image-inner-rectangle-border-width;
            border: $advantages-image-inner-rectangle-border-width solid $advantages-image-inner-rectangle-border-color;
            top: ($advantages-image-size - $advantages-image-inner-rectangle-size) / 2;
            left: -$advantages-image-inner-rectangle-shift;
            z-index: -1;
        }
    }

    .advantages-list {
        $margin-left: $advantages-image-inner-rectangle-shift + $advantages-image-margin-left + $advantages-image-size + $advantages-image-margin-right;

        list-style: none;
        padding: (($advantages-image-size - $advantages-image-inner-rectangle-size) / 2) 0 0;
        margin: 0 0 0 $margin-left;
    }

    .advantage {
        font-family: $font-family-default;
        font-size: $advantages-list-font-size;
        margin-bottom: $advantages-list-line-space;
        line-height: $advantages-list-line-height;
        padding-left: $advantages-list-icon-size + 10px;
        position: relative;

        &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 50%;
            width: $advantages-list-icon-size;
            height: $advantages-list-icon-size;
            margin-top: -($advantages-list-icon-size / 2);
            background: transparent url('../../../images/icons/check.png') center no-repeat;
        }
    }

    .description {
        clear: both;
        position: relative;
        background: $advantages-description-bg-color;
        color: $advantages-description-font-color;
        padding: $advantages-description-padding;
        margin-left: $advantages-description-margin-left;

        &:before {
            content: '';
            display: block;
            position: absolute;
            width: $advantages-description-icon-size;
            height: $advantages-description-icon-size;
            top: 50%;
            margin-top: -$advantages-description-icon-size / 2;
            left: -$advantages-description-icon-size;
            background: transparent url('../../../images/icons/i.png') right center no-repeat;
            z-index: 1;
        }

        &:after {
            content: '';
            position: absolute;
            width: $advantages-description-cutout-radius * 2;
            height: $advantages-description-cutout-radius * 2;
            border-radius: 50%;
            background: #fff;
            top: 50%;
            margin-top: -$advantages-description-cutout-radius;
            left: $advantages-description-cutout-size;
        }

        .text {
            position: relative;
            border: $advantages-description-inner-border-width solid $advantages-description-inner-border-color;
            border-radius: $advantages-description-inner-border-radius;
            padding: $advantages-description-inner-padding;

            &:before {
                content: '';
                position: absolute;
                width: $advantages-description-inner-before-rect-width;
                height: $advantages-description-inner-before-rect-height;
                border: $advantages-description-inner-border-width solid $advantages-description-inner-border-color;
                border-radius: $advantages-description-inner-border-radius;
                top: $advantages-description-inner-before-rect-position-top;
                left: $advantages-description-inner-before-rect-position-left;
            }

            &:after {
                content: '';
                width: $advantages-description-after-rect-size;
                height: $advantages-description-after-rect-size;
                position: absolute;
                border: $advantages-description-inner-border-width solid $advantages-description-inner-border-color;
                border-radius: $advantages-description-inner-border-radius;
                bottom: $advantages-description-after-rect-position-bottom;
                right: $advantages-description-after-rect-position-right;
            }
        }
    }

    @each $source in $sources {

        &.#{$source} {

            .image {
                background: url('../../../images/multiland/advantages/#{$source}.png') center no-repeat;
            }

        }

    }

    &.printers .image,
    &.viruses .image,
    &.routes .image {
        width: 554px;
        margin-left: 0;

        &:before {
            left: $advantages-image-inner-rectangle-shift + 40px;
        }
    }
}



