.company-prices {
    @import "variables";

    @media #{$adaptive-breakpoint-m} {

        .prices-list {
            padding: 30px;

            &:before {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-s} {
        padding: 30px 0;

        .prices-list {
            padding: 30px;

            &:before {
                display: none;
            }
        }

    }

    @media #{$adaptive-breakpoint-xs} {
        padding: 30px 0;

        .prices-list {
            padding: 10px;
            margin: 10px;

            &:before {
                display: none;
            }
        }

        .price {
            padding: 5px 120px 5px 5px;
            line-height: 1.5;

            &:before {
                display: none;
            }
        }

    }
}



