.company-facts {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        .value {
            font-size: 40px;
        }

    }

    @media #{$adaptive-breakpoint-m} {
        background: #f5f6f6;
        padding-bottom: 30px;

        .fact {
            width: 32%;
            margin-bottom: 20px;
            vertical-align: top;
        }

        .name {
            margin-bottom: 0;
        }

    }

    @media #{$adaptive-breakpoint-s} {
        background: #f5f6f6;
        padding-bottom: 30px;

        .fact {
            width: 32%;
            margin-bottom: 20px;
            vertical-align: top;
        }

        .value {
            font-size: 40px;
        }

        .name {
            margin-bottom: 0;
        }

    }

    @media #{$adaptive-breakpoint-xs} {
        background: #f5f6f6;
        padding-bottom: 30px;

        .facts-list {
            padding: 0 20px;
        }

        .fact {
            width: 100%;
            margin-bottom: 20px;
            vertical-align: top;
        }

        .name {
            margin-bottom: 0;
        }

    }
}



