.small-contacts {
    @import "variables";

    display: block;
    width: $small-contacts-width;

    .contacts-list {
        background: url('../../../images/icons/call-in.png') left center no-repeat transparent;
        list-style: none;
        margin: 0;
        font-weight: 700;
    }

    .contact {
        margin: 5px;
    }
}



