.company-warranties {
    @import "variables";

    @media #{$adaptive-breakpoint-m} {

        .warranties-list {

            &:after {
                display: none;
            }

        }

        .warranty {
            padding: 20px;
            margin-right: 15px;
            width: calc(50% - 15px);

            &:nth-child(2n) {
                padding: 20px;
                margin-right: 0;
                margin-left: 15px;
            }
        }

    }

    @media #{$adaptive-breakpoint-s} {

        .warranties-list {

            &:after {
                display: none;
            }

        }

        .warranty {
            display: block;
            padding: 20px;
            margin: 10px;
            width: auto;

            &:nth-child(2n) {
                display: block;
                padding: 20px;
                margin: 10px;
                width: auto;
            }
        }

    }

    @media #{$adaptive-breakpoint-xs} {

        .warranties-list {
            padding: 0 20px;

            &:after {
                display: none;
            }

        }

        .warranty {
            display: block;
            padding: 20px;
            width: auto;
            margin: 0 0 20px;

            &:nth-child(2n) {
                display: block;
                padding: 20px;
                width: auto;
                margin: 0 0 20px;
            }
        }

    }

    @media (max-width: 400px) {

        .warranty {

            .image {
                width: 50px;
                margin-right: 10px;
            }

            .name {
                width: calc(100% - 50px - 10px);
            }

            &:nth-child(2n) {

                .image {
                    margin-right: 0;
                    margin-left: 10px;
                }

            }

        }

    }
}



