body {
    background-color: $bg-color;
    font-family: $font-family-default;
    color: $main-color;
    overflow-x: hidden;
}

a {
    color: $main-color;
    text-decoration: none;
}

.content {
    width: $full-hd-width;
    max-width: $full-hd-width;
    margin: 0 auto;
    position: relative;
}

@each $source in $sources {

    .#{$source} {
        display: none;
    }

    .utm-#{$source} {

        .default {
            display: none;
        }

        .#{$source} {
            display: block;
        }
    }
}

.hidden {
    display: none;
}



