.client-reviews {
    @import "variables";

    @media #{$adaptive-breakpoint-s} {

        .review {
            width: 50%;
        }

    }

    @media #{$adaptive-breakpoint-xs} {

        .reviews-list {
            padding: 0 20px;
        }

        .review {
            width: 100%;
        }

    }
}



