.company-projects {
    @import "variables";

    @media #{$adaptive-breakpoint-l} {

        .projects-list {
            text-align: justify;
            -ms-text-align-last: center;
            text-align-last: center;
            text-justify: newspaper;
        }

        .project {
            width: 48%;
            margin-bottom: 20px;
        }

        .cost {
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;

            .amount {
                position: static;
                display: block;
                width: 100%;
                text-align: center;
                -ms-text-align-last: center;
                text-align-last: center;
                transform: none;
            }

        }

    }

    @media #{$adaptive-breakpoint-m} {

        .projects-list {
            text-align: justify;
            -ms-text-align-last: center;
            text-align-last: center;
            text-justify: newspaper;
        }

        .project {
            width: 48%;
            margin-bottom: 20px;
        }

        .cost {
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;

            .amount {
                position: static;
                display: block;
                width: 100%;
                text-align: center;
                -ms-text-align-last: center;
                text-align-last: center;
                transform: none;
            }

        }

    }

    @media #{$adaptive-breakpoint-s} {

        .projects-list {
            text-align: justify;
            -ms-text-align-last: center;
            text-align-last: center;
            text-justify: newspaper;
        }

        .project {
            width: 48%;
            margin-bottom: 20px;
        }

        .context {
            height: 160px;
        }

        .cost {
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;

            .amount {
                position: static;
                display: block;
                width: 100%;
                text-align: center;
                -ms-text-align-last: center;
                text-align-last: center;
                transform: none;
            }

        }

    }

    @media #{$adaptive-breakpoint-xs} {

        .projects-list {
            padding: 0 20px;
        }

        .project {
            display: block;
            width: auto;
            margin: 0 0 20px;
        }

        .cost {
            text-align: center;
            -ms-text-align-last: center;
            text-align-last: center;

            .amount {
                position: static;
                display: block;
                width: 100%;
                text-align: center;
                -ms-text-align-last: center;
                text-align-last: center;
                transform: none;
            }

        }

        .context {
            height: auto;
        }

    }
}



