.work-stages {
    @import "variables";

    margin-bottom: $stages-margin-bottom;

    .stages-list {
        counter-reset: stages;
        display: flex;
        font-size: 0;
        line-height: 0;
        list-style: none;
        padding: 0;
        margin: $stages-list-margin;
        text-align: justify;
        -ms-text-align-last: justify;
        text-align-last: justify;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .stage {
        position: relative;
        width: (100% / $stages-list-row-count) - 5%;
        flex-basis: (100% / $stages-list-row-count) - 5%;
        flex-shrink: 0;
        font-size: $stages-list-item-font-size;
        line-height: $stages-list-item-line-height;
        display: inline-block;
        padding: $stages-list-item-padding;
        box-sizing: border-box;
        background: $additional-color;
        text-align: center;
        -ms-text-align-last: center;
        text-align-last: center;

        &:after {
            content: '';
            position: absolute;
            display: block;
            right: -75px;
            width: 81px;
            height: 51px;
        }

        &:nth-child(2n + 1):after {
            top: -51px;
            background: url('../../../images/work-stages/uparrow.png') center no-repeat;
        }

        &:nth-child(2n):after {
            bottom: -51px;
            background: url('../../../images/work-stages/downarrow.png') center no-repeat;
        }

        &:last-child:after {
            display: none;
        }
    }

    @for $i from 0 to 5 {

        .stage-#{$i + 1} {

            .image {
                background-image: url('../../../images/work-stages/stage-#{$i + 1}.png');
            }
        }
    }

    .stage-title {
        height: calc(100% - 100px - 15px);
        min-height: 100px;
        position: relative;

        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            width: 60%;
            left: 20%;
            height: 1px;
            background: #fff;
        }

        &:after {
            top: auto;
            bottom: 0;
        }
    }

    .name {
        width: 100%;
        font-size: $stages-list-item-font-size;
        font-family: $font-family-default;
        margin: 0;
        font-weight: bold;
        color: #fff;
        text-align: center;
        padding: 5px 0;
        @include vcenter();
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .image {
        display: block;
        width: 100px;
        height: 100px;
        margin: 0 auto 15px;
        background: transparent center no-repeat;
        overflow: hidden;
        text-indent: -9999px;
    }

    .action-button {
        width: $stages-button-width;
        margin: 0 auto;
    }
}



