.headline {
    @import "variables";

    font-family: $font-family-headlines;
    font-size: $headline-font-size;
    text-transform: uppercase;
    text-align: center;
    margin: 0;
    word-wrap: break-word;
    overflow-wrap: break-word;

    .main,
    .side {
        display: block;
    }

    .break {
        display: block;
    }

    .side {
        margin-top: $headline-side-margin-top;
        font-family: $font-family-default;
        font-size: $headline-side-line-font-size;
        font-weight: $headline-side-line-font-weight;
    }
}



