.discounts {
    @import "variables";

    position: absolute;
    top: 130px;
    right: 0;
    z-index: 1;

    .discount {
        margin: 0 20px;
        position: relative;
        display: inline-block;
        cursor: pointer;
        vertical-align: top;
        visibility: hidden;

        &.discount-3 {
            position: absolute;
            top: 220px;
            right: 120px;
        }
    }

}



