.modal-window {
    @import "variables";

    position: fixed;
    background: rgba(0, 0, 0, 0.3);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;

    .form {
        display: block;
        width: 400px;
        max-width: 100%;
        background: #d0e3ee;
        @include default-box-shadow();
        box-sizing: border-box;
        padding: 60px 60px 40px;
        margin: 0 auto;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        .success,
        .error {
            display: none;
        }

        &.success {
            padding: 40px;

            * {
                display: none;
            }

            .success {
                display: block;
                font-size: 2em;
                color: $additional-color;
            }

        }

        &.error {

            .error {
                display: block;
                color: $accent-color;
            }

        }
    }
    $height: 2.5em;

    .input {

        display: block;
        width: 100%;
        margin-bottom: 10px;

        input, textarea {
            font-size: 1.3em;
            padding: 10px;
            display: block;
            width: 100%;
            height: $height;
            line-height: $height;
            border: 0;
            -webkit-box-sizing: border-box;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
        }

        .review {
            display: none;
            height: 4.5em;
            overflow-y: auto;
            line-height: normal;
        }

    }

    .send-review {
        display: none;
    }

    &.review {

        .send-review {
            display: inline;
        }

        .place-order {
            display: none;
        }

        .input .review {
            display: block;
        }

    }

    .description {
        text-align: center;
    }

    .action-button {
        width: 90%;
        margin: 20px auto 0;
        font-size: 1.5em;
        height: $height;
        line-height: $height;
    }
}



