.site-menu {
    @import "variables";

    display: block;

    ul {
        display: block;
        list-style: none;
        text-align: justify;
        -ms-text-align-last: justify;
        text-align-last: justify;
        padding: 0;
        margin: $menu-margin;
    }

    .menu-item {
        display: inline-block;
        text-transform: uppercase;

        a {
            font-size: $menu-font-size;
            padding: $menu-item-padding;
            border-bottom: $menu-item-underline-border-width solid $menu-item-underline-border-color;
            transition: border-bottom-color $menu-item-hover-transition-duration;
            font-weight: 600;

            &:hover {
                border-bottom-color: $menu-item-underline-border-color-hover;
            }
        }
    }
}



