.main-header {
    @import "variables";
    @import "../logo/variables";
    @import "../contacts/variables";

    height: $header-height;
    padding: $header-padding;
    @include default-box-shadow();

    .logo {
        position: relative;

        color: $logo-color;
        line-height: $header-height;
        float: left;
        width: 120px;

        top: 10px;

        img {
            width: 100%;
        }
    }

    .small-contacts {
        float: right;
        font-size: $header-font-size;
        line-height: $header-line-height;
        padding-top: 25px;

        .phone {
            font-size: 22px;
        }

        .email {
            font-size: 18px;
        }
    }

    .main-menu {
        margin-left: $logo-width;
        margin-right: $small-contacts-width;
        font-size: $header-font-size;
        line-height: $header-height;
    }
}



