.company-projects {
    @import "variables";

    margin-bottom: $projects-margin-bottom;

    .projects-list {
        margin-top: $projects-list-margin-top;
        padding: 0;
        list-style: none;
        font-size: 0;
        line-height: 0;
        text-align: justify;
        -ms-text-align-last: justify;
        text-align-last: justify;
    }

    .project {
        text-align: center;
        -ms-text-align-last: center;
        text-align-last: center;
        width: $projects-list-item-width;
        font-size: $projects-list-item-font-size;
        line-height: $projects-list-item-line-height;
        display: inline-block;
        background: $projects-list-item-bg-color;
        padding: $projects-list-item-padding;
        box-sizing: border-box;
        vertical-align: top;
        @include default-box-shadow();
        min-height: 575px;
    }

    .image {
        display: block;
        width: $projects-list-item-image-width;
    }

    .context {
        font-family: $font-family-default;
        font-size: $projects-list-item-context-font-size;
        text-transform: lowercase;
        height: 100px;

        .prefix {
            color: $projects-list-item-context-prefix-color;
            text-transform: uppercase;
            font-family: $font-family-marked;
            display: block;
            text-align: center;
        }
    }

    .time {
        display: block;
        padding: $projects-list-item-time-padding;
        background: $additional-color;
        color: $projects-list-item-time-font-color;
        font-family: $font-family-default;
        font-size: $projects-list-item-time-font-size;
    }

    .cost {
        font-family: $font-family-default;
        text-align: left;
        -ms-text-align-last: left;
        text-align-last: left;
        font-size: $projects-list-item-font-size;
        position: relative;

        .amount {
            font-family: $font-family-marked;
            @include vcenter();
            right: 0;
        }
    }
}



